* {
  box-sizing: border-box;
}
p {
  margin: 0;
  padding: 0;
}
button {
  border: none;
  cursor: pointer;
}
.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}



.footer {
  background: #212121;
  padding: 100px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}

.company1{
  display: flex;
  align-items: center;
  justify-content: center;
  color: white;
  gap: 12px;
  margin-top: 32px;
  margin-bottom: 12px;
}
.company1 > p {
  cursor: pointer;
}
.company2 {
  color: #CCC;
  font-size: 12px;
  line-height: 20px;
}
.company2 > p:nth-child(2) {
  margin-top: 32px;
}
.chat_logo {
  position: absolute;
  right: 42px;
  bottom: 44px;
}


.main_box1 {
  width: 100%;
  height: 800px;
  background-image: url(./img/main_img.png);
  display: flex;
  flex-direction: column;
  background-size: cover;
  background-position: center;
  align-items: center;
}
.main_box1::before {
  content: "";
  width: 100%;
  height: 800px;
  opacity: 0.7;
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background-color: #000;
  z-index: 0;
}
.header1 {
  width: 100%;
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  color: white;
}
.main_logo {
  margin-right: 100px;
}
.cate1 {
  width: 80px;
  margin: 0 10px 0 10px;
  height: 100%;
  font-weight: 800;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  overflow: hidden;
}
.login_btn {
  width: 88px;
  height: 39px;
  color: white;
  font-size: 14px;
  font-weight: 800;
  background-color: rgba(0, 0, 0, 0);
  border-radius: 100px;
  border: solid 1px white;
  margin-left: 100px;
  padding: 11px 24px 11px 24px;
}
.box1_title {
  width: 1280px;
  padding: 100px 0 100px 0;
  gap: 60px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.title_text {
  font-size: 64px;
  font-weight: 900;
  color: #FF820F;
  letter-spacing: 10px;
  position: relative;
}
.circle_box {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  gap: 100px;
}
.circle1 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 360px;
  height: 360px;
  border-radius: 180px;
  background-color: rgba(255, 255, 255, 0.5);
  position: relative;
  font-size: 28px;
  font-weight: 900;
}
.circle1:nth-child(2) {
  background-color: white;
}
.girl_text {
  margin-top: 20px;
}
.girl_button, .form_button {
  width: 75px;
  height: 39px;
  border-radius: 100px;
  padding: 12px 24px 12px 24px;
  margin-top: 16px;
  background-color: #212121;
  color: white;
  font-size: 14px;
  font-weight: 800;
}
.form_button {
  margin-top: 24px;
}
.girl_button2 {
  background-color: white;
  border: solid 1px #212121;
  color: #212121;
}
.main_box2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  background-color: white;
  padding: 100px 0 50px 0;
}
.swiper_box {
  display: flex;
  flex-direction: column;
  width: 1000px;
  
}
.feed, .board , .tip, .form{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 87px;
  height: 40px;
  border-radius: 100px;
  background: white;
  border: solid 1px #CCCCCC;
  color: #212121;
  gap: 4px;
  padding: 12px 20px 12px 20px;
  font-size: 14px;
  font-weight: 800;
}

.board {
  width: 127px;
  background: none;
}
.form {
  width: 87px;
  background: none;
}
.tip {
  width: 114px;
}

.feed_title, .tip_title, .form_title{
  display: flex;
  justify-content: space-between;
  align-items: center;
  text-align: left;
  font-size: 36px;
  font-weight: 800;
  padding-top: 20px;
  margin-bottom: 20px;
}
.tip_title {
  margin-bottom: 0;
}
.tip_title > div:nth-child(1) {
  display: flex;
  align-items: center;
}
.tip_title > div:nth-child(1) > div {
  display: flex;
  gap: 4px;
  margin-left: 20px;
}
.ft {
  margin-bottom: 40px;
}
.feedtitle > div:nth-child(1) {
  display: flex;
  justify-content: center;
  align-items: center;
}
.form_title {
  margin-bottom: 40px;
}
.feed_avatar {
  display: flex;
  align-items: center;
  text-align: left;
  font-size: 12px;
  font-weight: 800;
  color: #212121;
}
.feed_avatar > span {
  font-weight: 400;
  color: #999999;
  margin-left: 3px;
}
.feed_avatar > img {
  margin-right: 8px;
}





.mySwiper {
  width: 100%;
  padding-top: 20px !important;
  padding-bottom: 30px !important;
}

.mySwiper2 {
  width: calc(100% - 180px);
  padding-top: 20px !important;
  padding-bottom: 30px !important;
  margin: 0 auto;
}




.feed_slide_box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  
}
.feed_slide1 {
  display: flex;
  flex-direction: column;
  height: 420px;
  border: solid 1px #EEEEEE;
  border-radius: 0 60px 60px 60px;
  box-shadow: 4px 4px 30px 0 #8C90AB26;
  padding: 40px;
}
.feed_slide1 > img {
  width: 24px;
  height: 24px;
}
.feed_text {
  margin: 20px 0;
  text-align: left;
  font-size: 12px;
  color: #666666;
}
.feed_text > span {
  font-size: 24px;
  font-weight: 800;
  color: #212121;
}
.processBar1 {
  background: #FF820F;
  color: white;
  font-size: 10px;
  font-weight: 800;
  padding: 0 12px;
  height: 24px;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-bottom: 8px;
}
.processBar2 {
  background: #FFF8EC;
  color: white;
  font-size: 10px;
  font-weight: 800;
  height: 24px;
  border-radius: 100px;
  display: flex;
  align-items: center;
  margin-bottom: 40px;
  
}
.processBar2_1 {
  background: #FF820F;
  padding: 0 12px;
  height: 24px;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 200px;
}
.note {
  display: flex;
  flex-direction: column;
  border-radius: 0 24px 24px 24px;
  padding: 20px;
  background: #FFF8EC;
}
.note > p:nth-child(2) {
  margin-top: 12px;
  text-align: left;
  font-size: 14px;
  color: #212121;
}
.note > p:nth-child(3) {
  margin-top: 8px;
  font-size: 12px;
  text-align: left;
  color: #666666;
  cursor: pointer;
}

.mySwiper .swiper-slide {
  opacity: 0.25;
  transition: opacity 0.3s;
}
.mySwiper .swiper-slide-next,
.mySwiper .swiper-slide-next + .swiper-slide {
  opacity: 1;
}
.mySwiper2 .swiper-slide {
  opacity: 0.25;
  transition: opacity 0.3s;
}
.mySwiper2 .swiper-slide-next,
.mySwiper2 .swiper-slide-next + .swiper-slide,
.mySwiper2 .swiper-slide-next + .swiper-slide + .swiper-slide {
  opacity: 1;
}



.opacity {
  position: absolute;
  background: #ffffffb7;
  width: 450px;
  height: 430px;
  z-index: 1000;
  right: 0;
  top: 1040px;
}

.main_box3, .main_box5 {
  width: 100%;
  padding: 100px 0;
  background-color: #FFF8EC;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.board_box {
  padding: 40px;
  display: flex;
  align-items: center;
  text-align: left;
  width: 1000px;
  background: white;
  border: 1px solid var(--Grey_E, #EEE);
  margin-bottom: 12px;
  box-shadow: 4px 4px 20px 0px rgba(140, 144, 171, 0.10);
}
.board_box:nth-last-child(1) {
  margin-bottom: 0;
}
.board_box > div {
  display: flex;
  flex-direction: column;
}
.tag_box {
  display: flex;
  gap: 4px;
  
}
.board_title {
  color: #212121;
  font-size: 20px;
  font-weight: 800;
  cursor: pointer;

}
.board_post {
  display: inline-block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  color: #666;
  font-size: 14px;
  width: 760px;
  padding-right: 30px;
  margin-top: 12px;
  margin-bottom: 20px;
  cursor: pointer;

}
.board_tag, .tip_tag {
  padding: 8px 20px;
  border-radius: 100px;
  border: solid 1px #CCCCCC;
  color: #666666;
  background: white;
  font-size: 10px;
  width: 60px;
  margin-bottom: 40px;
}
.tip_tag {
  margin-bottom: 0px;
}
.board_avatar {
  display: flex;
  align-items: center;
  font-size: 12px;
  font-weight: 800;
  cursor: pointer;

}
.board_avatar > img {
  margin-right: 4px;
}
.board_avatar > span {
  display: flex;
  align-items: center;
  font-weight: 400;
  gap: 4px;
}
.board_avatar > span img {
  margin-left: 20px;
}
.board_img > img {
  width: 160px;
  height: 160px;
  cursor: pointer;
}

.main_box4 {
  background: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 100px 0 70px 0;
}
.tip_box1 {
  display: flex;
  flex-direction: column;
  box-shadow: 4px 4px 20px 0px rgba(140, 144, 171, 0.10);
  
}
.tip_box_img {
  background-image: url(./img/tip_img1.png);
  background-size: cover;
  height: 240px;
}
.tip_text {
  padding: 24px;
  text-align: left;
}
.tip_text p:nth-child(1) {
  color: #666666;
  font-size: 12px;
}
.tip_text p:nth-child(2) {
  color: #212121;
  font-size: 20px;
  font-weight: 800;
}
.form_box {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}
.form_start {
  padding: 40px;
  background: white;
  display: flex;
  justify-content: space-between;
  width: 1000px;
  border: 1px solid var(--Grey_E, #EEE);
box-shadow: 4px 4px 20px 0px rgba(140, 144, 171, 0.10);
}
.form_text > p:nth-child(1) {
  color: #666;
  font-size: 20px;
  font-weight: 900;
  letter-spacing: 2px;
}
.form_text > p:nth-child(2) {
  color: #212121;
  font-size: 32px;
  font-weight: 900;
}